import { Component, inject, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Chart } from 'chart.js/auto';
import { ReportService } from 'src/app/core/services/report/report.service';

@Component({
  selector: 'app-trail-balance-report',
  templateUrl: './trail-balance-report.component.html',
  styleUrls: ['./trail-balance-report.component.scss'],
})
export class TrailBalanceReportComponent {
  myForm: FormGroup;
  submitted: boolean = false;
  trailBalance: any = {};
  private modalService = inject(NgbModal);
  public chart: any;

  constructor(
    private formBuilder: FormBuilder,
    private reportService: ReportService
  ) {
    this.myForm = this.formBuilder.group({
      fromDate: [''],
      toDate: [''],
    });
    this.getTrailBalance();
  }
  getTrailBalance(submit?: boolean) {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      strStartDate: this.myForm.value.fromDate,
      strEndDate: this.myForm.value.toDate,
    };

    this.reportService.getTrailBalanceReport(obj).subscribe((res) => {
      submit ? (this.submitted = true) : (this.submitted = false);
      if (res.success) {
        this.trailBalance = res.data[0];

        if (submit) {
          this.chart.data.datasets = [
            {
              label: 'Debit',
              data: [
                0, // credit
                this.trailBalance.totalCostPrice, // debit
                this.trailBalance.totalExpenseDebitAmount, // credit
                this.trailBalance.customerBalance, // debit
                0, // credit
                this.trailBalance.totalTabbyAmount + // debit
                this.trailBalance.totalCreditCardAmount, // debit
              ],
              backgroundColor: '#FECACA',
            },
            {
              label: 'Credit',
              data: [
                this.trailBalance.totalSellingPrice, // credit
                0, // debit
                0, // credit
                0, // debit
                this.trailBalance.supplierBalance, // credit
                0, // debit
                // 0, // debit
              ],
              backgroundColor: '#bbf7d0',
            },
          ];
          this.chart.update();
        } else {
          setTimeout(() => {
            this.createChart();
          }, 100);
        }
      } else {
        this.trailBalance = [];
      }
    });
  }

  createChart() {
    this.chart = new Chart('MyChart', {
      type: 'bar',
      data: {
        labels: [
          'Selling Amount',
          'Cost Amount',
          'Total Expenses',
          'Assets',
          'Liability',
          'Service Charge',
          // 'Credit Card',
        ],
        datasets: [
          {
            label: 'Debit',
            data: [
              0, // credit
              this.trailBalance.totalCostPrice, // debit
              this.trailBalance.totalExpenseDebitAmount, // credit
              this.trailBalance.customerBalance, // debit
              0, // credit
              this.trailBalance.totalTabbyAmount + // debit
              this.trailBalance.totalCreditCardAmount, // debit
            ],
            backgroundColor: '#FECACA',
          },
          {
            label: 'Credit',
            data: [
              this.trailBalance.totalSellingPrice, // credit
              0, // debit
              0, // credit
              0, // debit
              this.trailBalance.supplierBalance, // credit
              0, // debit
              // 0, // debit
            ],
            backgroundColor: '#bbf7d0',
          },
        ],
      },
      options: {
        responsive: true,
        plugins: {
          legend: {
            position: 'top',
          },
          title: {
            display: true,
            text: 'Trail Balance',
          },
        },
      },
      // type: 'bar',
      // data: {
      //   labels: [
      //     'January',
      //     'February',
      //     'March',
      //     'April',
      //     'May',
      //     'June',
      //     'July',
      //   ],
      //   datasets: [
      //     {
      //       label: 'Monthly Sales',
      //       data: [65, 59, 80, 81, 56, 55, 40],
      //       backgroundColor: [
      //         'rgba(255, 99, 132, 0.2)',
      //         'rgba(54, 162, 235, 0.2)',
      //         'rgba(255, 206, 86, 0.2)',
      //         'rgba(75, 192, 192, 0.2)',
      //         'rgba(153, 102, 255, 0.2)',
      //         'rgba(255, 159, 64, 0.2)',
      //         'rgba(99, 132, 255, 0.2)',
      //       ],
      //       borderColor: [
      //         'rgba(255, 99, 132, 1)',
      //         'rgba(54, 162, 235, 1)',
      //         'rgba(255, 206, 86, 1)',
      //         'rgba(75, 192, 192, 1)',
      //         'rgba(153, 102, 255, 1)',
      //         'rgba(255, 159, 64, 1)',
      //         'rgba(99, 132, 255, 1)',
      //       ],
      //       borderWidth: 1,
      //     },
      //   ],
      // },
      // options: {
      //   scales: {
      //     y: {
      //       beginAtZero: true,
      //     },
      //   },
      // },
      // type: 'doughnut',
      // data: {
      //   labels: ['Verified Amount', 'Pending Amount'],
      //   datasets: [
      //     {
      //       data: [this.customerVerifedAmount, this.customerPendingAmount],
      //       backgroundColor: ['#13487F', '#BDB5B5'],
      //     },
      //   ],
      // },
      // options: {
      //   indexAxis: 'y',
      //   responsive: true,
      //   plugins: {
      //     legend: {
      //       display: false,
      //     },
      //   },
      //   scales: {
      //     x: {
      //       display: false,
      //       ticks: {
      //         display: false,
      //       },
      //       grid: {
      //         display: false,
      //       },
      //     },
      //     y: {
      //       display: false,
      //       ticks: {
      //         display: false,
      //       },
      //       grid: {
      //         display: false,
      //       },
      //     },
      //   },
      // },
    });
  }
  open(content: TemplateRef<any>) {
    this.modalService.open(content, {
      ariaLabelledBy: 'modal-basic-title',
      size: 'lg',
    });
    this.convertToPDF();
  }
  public convertToPDF() {
    setTimeout(() => {
      let printArea = document.getElementById('printArea');
      if (printArea) {
        let originalBody = document.body;
        let bodyElement = document.createElement('body');
        bodyElement.appendChild(printArea);
        document.body = bodyElement;
        window.print();
        document.body = originalBody;
      }
      this.modalService.dismissAll();
    }, 100);
  }
}
